import React from 'react'

const Image = (props) => {
    const {children, onClick} = props;

    return (
       <>
                
                <div className="icon clickable" onClick={onClick}>{children}</div>
               
                
                <style jsx>
                {`  
                .clickable {
            
                    ${onClick !== undefined ? 'cursor: pointer;\n' : ''}
                }
                .icon {
                        width: 40px;
                        height: 40px;

                        background: #4E7876;
                        border: 1px solid #225755;
                        border-radius: 3px;

                        display: flex;
                        align-items:center;
                        justify-content: center;

                    }

                `}

                </style>

        </>
                

    )
}

export default Image;